	import imgUpload from '@/components/poiuy-uImgUpload/imgUpload.vue'
	import {  
        mapMutations  
    } from 'vuex';
	export default{
		components:{
			imgUpload
		},
		data(){
			return {
				imgArray:[],
				item_name:"",
				item_price:"",
				item_desc:""
			}
		},
		onLoad(){
			if(this.$store.state.hasLogin == false){
				uni.navigateTo({
					url: '../login/login',
				})
			} 
		},
		onShow(){
			if(this.$store.state.hasLogin == false){
				this.$api.msg("Please Login First")
			}
		},
		methods: {
			...mapMutations(['login']),
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
				console.log(this.item_desc)
			},
			uploadItem:(item_name,item_price,item_desc)=>{
				return new Promise((resolve, reject) => {
					let jwt = uni.getStorageSync("jwt");
					console.log("jwt: " + jwt);
					uni.request({
						url:"http://3.96.104.51:53020/item/create",
						header:{Authorization: "Bearer " + jwt},
						data: {
							productName:item_name, 
							productDesc:item_desc, 
							productPrice:item_price,
							imgAddr: ""
						},
						method:"POST",
						timeout: 100000,
						dataType:"json",
						success:res => {
							console.log(res)
							console.log(item_desc)
							console.log(res.data.code)
							if(res.data.code == 200){
								resolve(res.data.data)
							}else{
								uni.showToast({
									title: "Please login"
								})
							} 
						},
						fail: function(e){
							reject(res.data.message)
						}
					})
				});
			},
			submit(){
				this.uploadItem(this.item_name, this.item_price, this.item_desc)
					.then((item_id) => {
						this.$refs.imgUpload.upload(item_id, res=>{
							
									   if(res.code==0){
									   		//0为正常返回，将回调的线上图片数组 赋值给需要提交的表单里
											//res.urlArray 线上路径图片数组
											//TODO
											uni.showToast({
												title: "商品上传成功"
											})
											this.item_desc = null
											this.item_price =""
											this.item_name = ""
										}else{
											uni.showToast({
												title: "商品上传失败"
											})
										}
						});
					})
			}
		},
		onReady(){
		}
	}
